import GlobalVars from "./globalvars";
import Annotation from "./annotation/annotation";
import Polygon from "./annotation/polygon";
import Constants from "./constants";

/**
 * SlideViewer object for controlling a viewer (of a study)
 *
 * @module slideviewer
 */

 export default class SlideViewer {
    constructor(containerId, slideId) {
        this._containerId = containerId;
        this._slideId = slideId;

        this._mainViewer = null;
        
        this._magZoomRatio = 0;
        this._zoomSlider = null;
        this._maxMagnification = undefined;

        this._arrAnnotations = [];
        this._annotationFunc = GlobalVars.ANNOTATION_TYPE.NONE;
        this._curActiveAnno = null;
        this._isShowAnnotations = true;

        this._curKeyImageId = '';
        this._sendingKeyImage = false;

        this._overlay = null;
        this._fabricCanvas = null;
        this._selection = null;

        this._isMouseDown = false;

        //is changed, need to save annotation list after a while
        this._isChanged = false;

        this.init();
    }

    init() {
        console.log('tileSource: ' + GlobalVars.urlDZIPrefix + this._slideId + ".dzi");
        this._mainViewer = OpenSeadragon({
            id: this._containerId,
            navigatorSizeRatio: 0.1,
            //wrapHorizontal: true,
            selectionButton: 'tool_createKeyImage',
            showNavigator: true,
            //navigatorPosition: "ABSOLUTE",
            //navigatorId: "navigatorDiv",
            showNavigationControl: false,
            showSequenceControl: false,
            prefixUrl: "/images/",
            tileSources: GlobalVars.urlDZIPrefix + this._slideId + ".dzi"
        });

        // thêm phương thức bị thiếu
        if(OpenSeadragon.Viewer.prototype.tiledImageViewportToImageZoom == undefined) {
            console.log('add method tiledImageViewportToImageZoom');
            OpenSeadragon.Viewer.prototype.tiledImageViewportToImageZoom = function (
                tiledImage,
                viewportZoom
            ) {
                console.log('tiledImageViewportToImageZoom, viewportZoom:' + viewportZoom); 
                tiledImage = tiledImage || this.world.getItemAt(0);
                viewportZoom = viewportZoom || this.viewport.getZoom();
                var ratio =
                    (tiledImage._scaleSpring.current.value *
                        tiledImage.viewport._containerInnerSize.x) /
                    tiledImage.source.dimensions.x;
                return ratio * viewportZoom;
            };
        }
        
        this._mainViewer.scalebar({
            minWidth: "250px",
            type: OpenSeadragon.ScalebarType.RULER, // RULER_SCALEBAR
            location: OpenSeadragon.ScalebarLocation.LEFT_MIDDLE, // BOTTOM_MIDDLE
            pixelsPerMeter: 1 / GlobalVars.currentSlide._xRes * 1e6,
            stayInsideImage: false,
            barThickness: 2,
            yOffset: 5,
            hatchMark: 10,
            numMark: 10,
            //sizeAndTextRenderer: OpenSeadragon.ScalebarSizeAndTextRenderer.IMPERIAL_LENGTH,
            //sizeAndTextRenderer: OpenSeadragon.ScalebarSizeAndTextRenderer.ASTRONOMY
        });

        this._initSlider();

        setTimeout(() => {
            if(this._calcMagZoomRatio() == false) {
                setTimeout(() => {
                    if(this._calcMagZoomRatio() == false) {
                        setTimeout(() => {
                            this._calcMagZoomRatio()
                        }, 10000);
                    }
                }, 10000);
            }
        }, 1000);

        var _self = this;
        this._mainViewer.addHandler("viewport-change", function () {
            let zoom = _self._getMagZoom();
            _self._zoomSlider.value(zoom);
            $("#current-magnification").html(`${zoom.toFixed(2)} `);
        });
    
        this._mainViewer.addHandler("zoom", function () {
            let zoom = _self._getMagZoom();
            if (isNaN(zoom)) return;
            if(_self._maxMagnification != undefined)
                $("#zoom-level").html(`${zoom.toFixed(0)} / ${_self._maxMagnification.toFixed(0)}`);
            else
                $("#zoom-level").html(`${zoom.toFixed(0)} / ...`);

            //update annotations size if needed
            _self.updateAnnotationSizes();
        });

        //fabric overlay
        let options = {
            /*Scale phụ thuộc vào kích thước thực tế của bức ảnh */
            scale: 1000,
            // containerPadding vốn không có trong source OSD fabric gốc
            // containerPadding là padding của ô div chứa OSD element
            // Cần tính thêm containerPadding trong quá trình tính offset cho đối tượng fabric khi zoom
            containerPaddingLeft: 0,
            containerPaddingTop: 0
        };
        this._overlay = this._mainViewer.fabricjsOverlay(options);
        this._fabricCanvas = this._overlay.fabricCanvas();
        this._initFabricCanvas();

        this._initSelection();
    }

    dispose() {
        //release resources here

    }

    openNewSlide(slideId) {
        this._slideId = slideId;
        let tileSource = GlobalVars.urlDZIPrefix + this._slideId + ".dzi";
        this._mainViewer.open(tileSource);
        
        this._mainViewer.scalebar({
            minWidth: "250px",
            pixelsPerMeter: 1 / GlobalVars.currentSlide._xRes * 1e6
        });
    }

    _calcMagZoomRatio() {
        let tiledImage = this._mainViewer.world.getItemAt(0);
        console.log('_calcMagZoomRatio, tiledImage:', tiledImage);
        if(tiledImage == undefined)
            return false;
        
        let ratio = (tiledImage._scaleSpring.current.value * tiledImage.viewport._containerInnerSize.x) / tiledImage.source.dimensions.x;
        this._magZoomRatio = ratio * 100;

        //update related UI
        this._maxMagnification = this._magZoomRatio * this._mainViewer.viewport.getMaxZoom();
        $('#max-magnification').html(this._maxMagnification.toFixed(2) + 'x');
        this._zoomSlider.setOptions({
            min: this._mainViewer.viewport.getMinZoom() * this._magZoomRatio,
            max: this._mainViewer.viewport.getMaxZoom() * this._magZoomRatio
        });
        return true;
    }

    _getMagZoom() {
        return this._mainViewer.viewport.getZoom() * this._magZoomRatio;
    }

    _initSlider() {
        var _self = this;
        let center = this._mainViewer.viewport.getCenter();
        this._zoomSlider = $("#zoomSlider")
            .kendoSlider({
                tooltip: {
                    enable: true,
                },
                showButtons: false,
                smallStep: 0.01,
                // đặt lại slider
                min: 0,
                max: 1000,
                //min: viewer.viewport.getMinZoom(),
                //max: viewer.viewport.getMaxZoom()
                tickPlacement: "none",
                slide: function (e) {
                    // console.log("slide " + e);
                    _self.fitTo(center, e.value, true);
                },
                change: function (e) {
                    // console.log("change " + e);
                    _self.fitTo(center, e.value, true);
                },
            })
            .data("kendoSlider");
    }

    fitTo(pointer, magZoom, imm) {
        //console.log(pointer);
        if(this._magZoomRatio == 0) {
            this.calcMagZoomRatio();
            if(this._magZoomRatio == 0) {
                console.log("DEV: cannot calculate magZoomRatio!!!");
                return;
            }
        }
        magZoom /= this._magZoomRatio;
        // let point = this._mainViewer.viewport.windowToViewportCoordinates(pointer);
        let center = this._mainViewer.viewport.getCenter();
        let cZoom = this._mainViewer.viewport.getZoom();
        let ratio = magZoom / cZoom;
        let bounds = this._mainViewer.viewport.getBounds();
        //let boundTopLeft = this._mainViewer.viewport.viewportToWindowCoordinates(new OpenSeadragon.Point(bounds.x, bounds.y));
        //let boundBottomRight = this._mainViewer.viewport.viewportToWindowCoordinates(new OpenSeadragon.Point(bounds.x + bounds.width, bounds.y + bounds.height));
        //let width = boundBottomRight.x - boundTopLeft.x;
        //let height = boundBottomRight.y - boundTopLeft.y;
        center.x = (center.x - pointer.x) / ratio + pointer.x;
        center.y = (center.y - pointer.y) / ratio + pointer.y;
        bounds.width /= ratio;
        bounds.height /= ratio;
        bounds.x = center.x - bounds.width / 2;
        bounds.y = center.y - bounds.height / 2;
        this._mainViewer.viewport.fitBounds(bounds, imm);
    }

    fitAuto() {
        this._mainViewer.viewport.fitBounds(this._mainViewer.viewport.getHomeBounds());
    }

    /**
     * Functions for annotation (Fabric overlays)
     * */
    _initFabricCanvas() {
        var fabricCanvas = this._fabricCanvas;

        fabricCanvas.freeDrawingBrush.color = "#539BDE";
        fabricCanvas.freeDrawingBrush.width = 2;

        var _self = this;
        this._fabricCanvas.on("mouse:down", function (options) {
            console.log('mousedown');
            if(_self._annotationFunc == GlobalVars.ANNOTATION_TYPE.NONE)
                return;
                
            if(_self._annotationFunc == GlobalVars.ANNOTATION_TYPE.POLYGON) {
                if(_self._curActiveAnno != null && _self._curActiveAnno._isFinished == false) {
                    let polygon = _self._curActiveAnno;
                    if(polygon.onCreatingClick(options)) {
                        //polygon created
                        console.log('finish creating polygon');
                        _self._mainViewer.setMouseNavEnabled(true);
                        _self._mainViewer.outerTracker.setTracking(true);
                    }
                }
                else {
                    let pointer = _self._fabricCanvas.getPointer(options.e);
                    let uuid = _self.generateUUID();
                    let annotation = new Polygon(_self._annotationFunc, _self, _self._fabricCanvas, uuid, pointer);
                    _self._arrAnnotations.push(annotation);
                    _self.setActiveAnnotation(annotation);
                    _self._isMouseDown = true;
                    _self._mainViewer.setMouseNavEnabled(false);
                    _self._mainViewer.outerTracker.setTracking(false);
                }
                return;
            }

            if(_self._isMouseDown)
                return;

            _self._isMouseDown = true;
            _self._mainViewer.setMouseNavEnabled(false);
            _self._mainViewer.outerTracker.setTracking(false);

            //console.log(o);
            let pointer = _self._fabricCanvas.getPointer(options.e);
            let uuid = _self.generateUUID();
            let annotation = new Annotation(_self._annotationFunc, _self, _self._fabricCanvas, uuid, pointer);
            _self._arrAnnotations.push(annotation);
            _self.setActiveAnnotation(annotation);

            // console.log('mousedown end, ' + (_self._curActiveAnno != null) + ', ' + _self._isMouseDown);

            // let virtualMouseDown = new MouseEvent("pointerdown", o.e);
            // try {
            //     //console.log(virtualMouseDown);
            //     fabricCanvas.upperCanvasEl.dispatchEvent(virtualMouseDown);
            // } catch (e) {
            //     console.log(e);
            // }
        });

        this._fabricCanvas.on("mouse:move", function (o) {
            // console.log('mousemove', _self._curActiveAnno);
            if(_self._curActiveAnno == null || !_self._isMouseDown)
                return;
            // console.log('mousemove here');
            let pointer = fabricCanvas.getPointer(o.e);
            _self._curActiveAnno.onCreating(pointer);
            _self._fabricCanvas.renderAll();
        });

        this._fabricCanvas.on("mouse:up", function (o) {
            console.log('mouseup');
            if (_self._curActiveAnno == null || !_self._isMouseDown) {
                _self._mainViewer.setMouseNavEnabled(true);
                _self._mainViewer.outerTracker.setTracking(true);
                return;
            }

            if(_self._annotationFunc != GlobalVars.ANNOTATION_TYPE.POLYGON) {
                _self._curActiveAnno.onFinish();
                _self._mainViewer.setMouseNavEnabled(true);
                _self._mainViewer.outerTracker.setTracking(true);
                _self._isMouseDown = false;
            }
            else {
                //do nothing?
            }
            
        });

        // zoom viewport khi lăn chuột
        this._fabricCanvas.on("mouse:wheel", function (o) { });

        // // hight light annotation khi được chọn
        // fabricCanvas.on("mouse:down", function (o) {
        //     // console.log("mouse:down");s
        //     selectingObj = fabricCanvas.getActiveObject();
        //     updateFabricObjectColor();
        // });

        this._fabricCanvas.on("object:moving", function (o) { 
            console.log('object:moving');
            if(_self._curActiveAnno != null && _self._curActiveAnno._uuid === o.target.uuid) {
                _self._curActiveAnno.onMove(o.target);
                _self._fabricCanvas.renderAll();
                _self.setIsChanged();
            }
                
        });

        this._fabricCanvas.on("object:selected", function (o) {
            _self.selectAnnotation(o.target);
        });

        this._fabricCanvas.on("object:scaling", function (o) {
            if(_self._curActiveAnno != null && _self._curActiveAnno._uuid === o.target.uuid) {
                _self._curActiveAnno.onMove(o.target);
                _self._fabricCanvas.renderAll();
                _self.setIsChanged();
            }
        });

        this._fabricCanvas.on('path:created', function(opt) {
            // // opt.path is a fabric.Path ready to use and already on the canvas
            // console.log(opt.path instanceof fabric.Path)
            // // this is the path data
            // console.log(opt.path.path);
            // console.log(opt.path.path.map(chunk => {
            //     return chunk.join(' ');
            // }).join(' '))

            if(_self._curActiveAnno != null) {
                _self._curActiveAnno.setFreeDrawingPath(opt.path);
                _self._curActiveAnno.onFinish();
            }
        });
    }
    
    refreshFabricCanvas() {
        this._fabricCanvas.renderAll();
    }

    _initSelection() {
        var _self = this;
        var options_selection = {
            element: null, // html element to use for overlay
            showSelectionControl: false, // show button to toggle selection mode
            toggleButton: null, // dom element to use as toggle button
            showConfirmDenyButtons: true,
            styleConfirmDenyButtons: true,
            returnPixelCoordinates: true,
            keyboardShortcut: '', // key to toggle selection mode
            rect: null, // initial selection as an OpenSeadragon.SelectionRect object
            allowRotation: false, // turn selection rotation on or off as needed
            startRotated: false, // alternative method for drawing the selection; useful for rotated crops
            startRotatedHeight: 0.1, // only used if startRotated=true; value is relative to image height
            restrictToImage: true, // true = do not allow any part of the selection to be outside the image

            // thuộc tính tự định nghĩa
            useConstraint: GlobalVars.userSettings.UseKeyImageConstraint,
            constraintWidth: GlobalVars.userSettings.KeyImageConstraintWidth,
            constraintHeight: GlobalVars.userSettings.KeyImageConstraintHeight,
            configBtn: function () {
                var config = document.createElement('div');
                config.style.marginRight = '5px';
                config.className += 'btn-config';
                config.innerHTML = '<i class="fas fa-tools"></i>';
                config.onclick = function () {
                    GlobalVars.showUserSettings();
                };
                return config;
            },

            onSelection: function (rect) {
                // send key image
                // var selectionRect = rect;
                // var windowRect = viewer.viewport.viewportToImageRectangle(viewer.viewport.getBounds());
                // //console.log(viewer.viewport.viewportToImageRectangle(viewer.viewport.getBounds()));
                // createKeyImage(selectionRect, windowRect);

                _self.createKeyImage(rect);
            }, // callback
            /**
             * thuộc tính onCancel không có trong selectionJS
             * thuộc tính này mình tự thêm vào, có chỉnh sửa mã nguồn của selectionJS
             * Định nghĩa hành động cần thêm khi hủy vẽ selection
             * */
            onCancel: function () {
                _self.onCancelKeyImage();
            },
            inputEvent: {
                keydown: function (e) {
                    //alert('gõ cjeets nè');
                    if (parseInt(e.key)) {
                        e.stopPropagation();
                    }
                    //if (e.key)
                    else {
                        e.stopPropagation();
                    }
                }
            },
            prefixUrl: './styles/images/', // overwrites OpenSeadragon's option
            navImages: { // overwrites OpenSeadragon's options
                selection: {
                    REST: 'selection_rest.png',
                    GROUP: 'selection_grouphover.png',
                    HOVER: 'selection_hover.png',
                    DOWN: 'selection_pressed.png'
                },
                selectionConfirm: {
                    REST: 'selection_confirm_rest.png',
                    GROUP: 'selection_confirm_grouphover.png',
                    HOVER: 'selection_confirm_hover.png',
                    DOWN: 'selection_confirm_pressed.png'
                },
                selectionCancel: {
                    REST: 'selection_cancel_rest.png',
                    GROUP: 'selection_cancel_grouphover.png',
                    HOVER: 'selection_cancel_hover.png',
                    DOWN: 'selection_cancel_pressed.png'
                },
            },
            showButton: false,
            transition: 100
        };
        this._selection = this._mainViewer.selection(options_selection);
    }

    enableKeyImage() {
        if (this._selection.isSelecting) 
            return;
        this._selection.toggleState();
    }
    
    disableKeyImage() {
        if (!this._selection.isSelecting) return;
        this._selection.toggleState();
        if (this._selection) {
            // selection.cancel();
        }
        $(".keyimage-container .image-container").removeClass("selected");
    }

    /**
     * Tạo UUID cho fabric object
     * */
    generateUUID() {
        var d = new Date().getTime();
        if (window.performance && typeof window.performance.now === "function") {
            d += performance.now(); //use high-precision timer if available
        }
        var uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (
            c
        ) {
            var r = (d + Math.random() * 16) % 16 | 0;
            d = Math.floor(d / 16);
            //console.log(r);
            return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
        });
        return uuid;
    }

    setActiveAnnotation(annotation) {
        if(this._curActiveAnno != null)
            this._curActiveAnno.setActive(false);
        
        this._curActiveAnno = annotation;
        annotation.setActive(true);
        this._fabricCanvas.renderAll();
    }

    selectAnnotation(fabricObj) {
        console.log('selectAnnotation', fabricObj);
        this.selectAnnotationByUuid(fabricObj.uuid, true);
    }

    selectAnnotationByUuid(uuid, needScroll) {
        console.log('selectAnnotationByUuid: ' + uuid);
        let anno = this._arrAnnotations.find((en) => en._uuid == uuid);
        if(anno != undefined) {
            this.setActiveAnnotation(anno);

            //show active item in annotation list & scroll to it
            $('#annotations-box div').removeClass('li-active');
            $('#' + anno._uuid).addClass('li-active');
            if(needScroll == true) {
                $('#annotations-box').animate({
                    scrollTop: $('#' + anno._uuid).offset().top
                }, 500);
            }
        }
        else {
            console.log('Not found annotation with uuid: ' + uuid + '!!!');
        }
    }

    setAnnotationFunc(func) {
        this._annotationFunc = func;
        if(func == GlobalVars.ANNOTATION_TYPE.FREEHAND) {
            this._fabricCanvas.isDrawingMode = true;
        }
        else {
            this._fabricCanvas.isDrawingMode = false;
        }
    }

    onAnnotationCreated(uuid, label) {
        //save new annotation to server
        this.saveListAnnotations();

        //show in list
        let container = $("#annotations-box");
        this.show1Annotation(uuid, label, container, this._arrAnnotations.length);
    }

    removeAnnotation(uuid) {
        //safety check if it's current selected
        if(this._curActiveAnno != null && this._curActiveAnno._uuid === uuid)
            this._curActiveAnno = null;

        let len = this._arrAnnotations.length;
        for(let i = 0; i < len; i++) {
            let anno = this._arrAnnotations[i];
            if(anno._uuid == uuid) {
                this._arrAnnotations.splice(i, 1);
                anno.dispose();

                break;
            }
        }
    }

    removeCurrentAnnotation() {
        if(this._curActiveAnno != null) {
            let cur = this._curActiveAnno;
            this._curActiveAnno = null;

            let len = this._arrAnnotations.length;
            for(let i = 0; i < len; i++) {
                let anno = this._arrAnnotations[i];
                if(anno._uuid == cur._uuid) {
                    console.log('here');
                    this._arrAnnotations.splice(i, 1);
                    anno.dispose();

                    break;
                }
            }

            cur = undefined;
            this._fabricCanvas.renderAll();

            //update list annotations to server
            this.saveListAnnotations();
        }
    }

    showHideAnnotations() {
        this._isShowAnnotations = !this._isShowAnnotations;
        console.log('show: ' + this._isShowAnnotations);

        let len = this._arrAnnotations.length;
        for(let i = 0; i < len; i++) {
            let anno = this._arrAnnotations[i];
            if(this._isShowAnnotations) {
                anno.show();
            }
            else {
                anno.hide();
            }
        }
        this._fabricCanvas.renderAll();
    }

    /**
     * Create keyimage data then upload to server
     * @param selectionRect tọa độ khung keyimage được chọn, tính theo pixel, dưới dạng openseadragon rect
     * @param windowRect tọa độ cửa sổ màn hình đang chọn keyimage, tính theo pixel
     */
    createKeyImage(selectionRect, windowRect, title, note) {
        this._sendingKeyImage = true;

        if(windowRect == undefined)
            windowRect = this._mainViewer.viewport.viewportToImageRectangle(this._mainViewer.viewport.getBounds());

        if(title == undefined)
            title = $("#keyImageTitle").val();
            
        if(note == undefined)
            note = $("#keyImageNote").val();
        
        let data = {
            SlideId: this._slideId,
            x: parseInt(selectionRect.x),
            y: parseInt(selectionRect.y),
            w: parseInt(selectionRect.width),
            h: parseInt(selectionRect.height),
            ScreenX: parseInt(windowRect.x),
            ScreenY: parseInt(windowRect.y),
            ScreenW: parseInt(windowRect.width),
            ScreenH: parseInt(windowRect.height),
            ImagePath: '',
            Title: title,
            Note: note,
            AnnotationBase64: this._cropAnnotationOverlay(selectionRect),
            Username: 'leht'
        };
        // console.log('createKeyImage:', data);
        this._sendKeyImage(data);

        $("#keyImageTitle").val('');
        $("#keyImageNote").val('');
    }

    _sendKeyImage(keyimageData) {
        let url = GlobalVars.domainSlide + Constants.URL_SAVE_KEYIMAGE;

        let apiMethod = 'POST';
        if(this._curKeyImageId !== '') {
            apiMethod = 'PUT';
            url += '/' + this._curKeyImageId;
        }

        var _self = this;
    
        $.ajax({
            url: url,
            method: apiMethod,
            data: JSON.stringify(keyimageData),
            contentType: 'application/json',
            success: function (res) {
                console.log(res);
                if (res.isValid) {
                    _self._processSendKeyImageResponse(res.jsonData);
                }
            }
        });
    }

    _processSendKeyImageResponse(jsonData) {
        $.alert({
            title: '<span style="color:green;">Success</span>',
            content: 'Save key image successfully!',
        });

        if(this._curKeyImageId === '') {
            //add to list keyimages
            let slide = GlobalVars.currentSlide;
            let keyImg = jsonData;
            slide._arrKeyImages.push(keyImg);

            let container = $("#images-box");
            GlobalVars.show1KeyImage(keyImg, container);

            $('#lblKeyImageCount').html('(' + slide._arrKeyImages.length + ')');
        }
        else {
            //find key image from current slide
            let keyImg;
            let slide = GlobalVars.currentSlide;
            let len = slide._arrKeyImages.length;
            for(let i = 0; i < len; i++) {
                if(slide._arrKeyImages[i].id === this._curKeyImageId) {
                    keyImg = slide._arrKeyImages[i];
                    Object.assign(slide._arrKeyImages[i], jsonData);
                    break;
                }
            }
            
            if(keyImg == undefined) {
                console.log('DEV - Not found key image by id!!! ' + this._curKeyImageId);
                return;
            }

            GlobalVars.update1KeyImage(this._curKeyImageId, jsonData);
        }
        this._sendingKeyImage = false;
    }

    onCancelKeyImage() {
        console.log('onCancelKeyImage');
        // keyImageMethod = 'POST';
        // $('.keyimage-container .image-container').removeClass('selected')
        // // console.log(keyImageMethod);

        if(!this._sendingKeyImage)
            this._curKeyImageId = '';

        $("#keyImageTitle").val('');
        $("#keyImageNote").val('');
    }

    _cropAnnotationOverlay(selectionRect) {
        let rect = this._selectionRectToViewerElementRect(selectionRect);
        let cropping = {
            top: rect.y,
            left: rect.x,
            width: rect.width,
            height: rect.height,
        };
        // let cropping = {
        //     top: selectionRect.y,
        //     left: selectionRect.x,
        //     width: selectionRect.width,
        //     height: selectionRect.height,
        // };
        console.log('cropping: (' + cropping.left + ', ' + cropping.top + ', ' + cropping.width + ', ' + cropping.height + ')');
        let img = this._fabricCanvas.toCanvasElement(4, cropping).toDataURL();
        // let img = this._fabricCanvas.toCanvasElementNoTransform(4, cropping).toDataURL();

        //debug
        // let newTab = window.open('about:blank', '_blank');
        // newTab.document.write(`<html>
        //     <head>
        //     </head>
        //     <body>
        //         <img src='${img}' />
        //     </body>
        //     </html>`);

        return img;
    }

    _selectionRectToViewerElementRect(selectionRect) {
        let topLeft = this._mainViewer.viewport.imageToViewerElementCoordinates(
            new OpenSeadragon.Point(selectionRect.x, selectionRect.y)
        );
        let bottomRight = this._mainViewer.viewport.imageToViewerElementCoordinates(
            new OpenSeadragon.Point(
                selectionRect.x + selectionRect.width,
                selectionRect.y + selectionRect.height
            )
        );
        return new OpenSeadragon.Rect(
            topLeft.x,
            topLeft.y,
            bottomRight.x - topLeft.x,
            bottomRight.y - topLeft.y
        );
    }

    focusOnKeyImage(keyImageId) {
        //find key image from current slide
        let keyImg;
        let slide = GlobalVars.currentSlide;
        let len = slide._arrKeyImages.length;
        for(let i = 0; i < len; i++) {
            if(slide._arrKeyImages[i].id === keyImageId) {
                keyImg = slide._arrKeyImages[i];
                break;
            }
        }

        if(keyImg == undefined) {
            console.log('DEV - Not found key image by id!!! ' + keyImageId);
            return;
        }

        //edit key image
        this.editKeyImage(keyImg.id, keyImg.title, keyImg.note, {
            x: keyImg.x,
            y: keyImg.y,
            width: keyImg.w,
            height: keyImg.h
        });
    }

    editKeyImage(keyImageId, title, note, rect) {
        let bounds = {
            x: rect.x - rect.width * 0.3,
            y: rect.y - rect.height * 0.3,
            w: rect.width * 1.6,
            h: rect.height * 1.6,
        };

        // unselect other functions
        this.setAnnotationFunc(GlobalVars.ANNOTATION_TYPE.NONE);
    
        try {
            this._mainViewer.removeOverlay("runtime-overlay");
        }
        catch (e) { }

        this._curKeyImageId = keyImageId;

        let r = this._mainViewer.viewport.imageToViewportRectangle(rect.x, rect.y, rect.width, rect.height);
        let sr = this._mainViewer.viewport.imageToViewportRectangle(bounds.x, bounds.y, bounds.w, bounds.h);

        this._mainViewer.viewport.fitBounds(new OpenSeadragon.Rect(sr.x, sr.y, sr.width, sr.height, 0), false);
    
        //console.log(r);
        //console.log(selection.rect);
        this._selection.rect = new OpenSeadragon.SelectionRect(r.x, r.y, r.width, r.height);
        this._selection.draw();
        
        $("#keyImageTitle").val(title);
        $("#keyImageNote").val(note);
    }

    deleteKeyImage(keyImageId) {
        //find key image from current slide
        let keyImg;
        let slide = GlobalVars.currentSlide;
        let len = slide._arrKeyImages.length;
        for(let i = 0; i < len; i++) {
            if(slide._arrKeyImages[i].id === keyImageId) {
                keyImg = slide._arrKeyImages[i];
                break;
            }
        }

        if(keyImg == undefined) {
            console.log('DEV - Not found key image by id!!! ' + keyImageId);
            return;
        }

        //call API delete key image
        var _self = this;
        parent.deleteKeyImage(keyImageId, function (res) {
            console.log('deleteKeyImage res:', res);
    
            //remove keyImg from list
            $('#' + keyImageId).remove();

            let slide = GlobalVars.currentSlide;
            let len = slide._arrKeyImages.length;
            for(let i = 0; i < len; i++) {
                if(slide._arrKeyImages[i].id === keyImageId) {
                    slide._arrKeyImages.splice(i, 1);
                    $('#lblKeyImageCount').html('(' + (len-1) + ')');
                    break;
                }
            }

            //unfocus if needed
            if(_self._curKeyImageId === keyImageId) {
                _self._selection.undraw();

                _self._curKeyImageId = '';

                $("#keyImageTitle").val('');
                $("#keyImageNote").val('');
            }
        });
    }

    getListKeyImages() {
        // $.ajax({
        //     url: GlobalVars.domain + Constants.URL_GET_KEYIMAGES + slideId,
        //     method: 'GET',
        //     success: function (res) {
        //         console.log(res);
        //         if (res.d.IsValid) {
        //             _showListKeyImages(res.d.JsonData);
        //         }
        //     }
        // });
    
        var slideId = this._slideId;
        parent.getListKeyImages(slideId, function (res) {
            console.log('getListKeyImages:', res);
            GlobalVars._showListKeyImages(slideId, res);
        });
    }

    removeAllAnnotations() {
        //remove old annotations
        let len = this._arrAnnotations.length;
        if(len > 0) {
            for(let i = 0; i < len; i++) {
                let anno = this._arrAnnotations[i];
                anno.dispose();
            }
            this._arrAnnotations = [];
            this.refreshFabricCanvas();
        }
    }

    getListAnnotations() {
        this.removeAllAnnotations();

        //get new list
        var _self = this;
        parent.getListAnnotations(this._slideId, function (res) {
            console.log('getListAnnotations:', res);
            console.log('current zoom: ' + _self._fabricCanvas.getZoom());
            //TODO: create list annotations & show
            let len = res.length;
            for(let i = 0; i < len; i++) {
                let dataObj = res[i];
                if(dataObj.type != GlobalVars.ANNOTATION_TYPE.POLYGON) {
                    _self._arrAnnotations.push(Annotation.createFromDataObj(dataObj, _self, _self._fabricCanvas));
                }
                else {
                    let polygon = Polygon.createFromDataObj(dataObj, _self, _self._fabricCanvas);
                    if(polygon != null)
                        _self._arrAnnotations.push(polygon);
                }
            }
            _self.refreshFabricCanvas();

            _self.showListAnnotations();
            _self.showAnnotationCount();

            setTimeout(function() {
                _self.updateAnnotationSizes();
            }, 100);
        });
    }
    
    saveListAnnotations() {
        console.log('saveListAnnotations');
        console.log('current zoom: ' + this._fabricCanvas.getZoom());
        let list = [];

        //gen list from this._arrAnnotations
        let len = this._arrAnnotations.length;
        for(let i = 0; i < len; i++) {
            let anno = this._arrAnnotations[i];
            let dataObj = anno.getAnnotationData4Server();

            dataObj['SlideId'] = this._slideId;
            dataObj["ZoomLevel"] = this._fabricCanvas.getZoom();
            dataObj['UserId'] = GlobalVars.userId;

            list.push(dataObj);
        }

        this._isChanged = false;

        let data = {
            slideId: this._slideId,
            listAnnotation: JSON.stringify(list)
        };
        parent.saveListAnnotations(data, function (res) {
            console.log('saveListAnnotations:', res);
            //TODO: create list annotations & show
        });
    }

    setIsChanged() {
        if(!this._isChanged) {
            this._isChanged = true;
            var _self = this;
            setTimeout(function() {
                _self.saveListAnnotations();
            }, 2000);
        }
    }

    updateAnnotationSizes() {
        let len = this._arrAnnotations.length;
        if(len == 0)
            return;

        let curZoom = this._fabricCanvas.getZoom();
        for(let i = 0; i < len; i++) {
            let anno = this._arrAnnotations[i];
            anno.updateSizeByZoom(curZoom);
        }
        this.refreshFabricCanvas();
    }

    showListAnnotations() {
        let container = $("#annotations-box");
        container.html('');

        let len = this._arrAnnotations.length;
        for(let i = 0; i < len; i++) {
            let anno = this._arrAnnotations[i];
            this.show1Annotation(anno._uuid, anno._label, container, i + 1);
        }
    }

    show1Annotation(uuid, label, container, idx) {
        let keyImgDiv = $('<div>').attr('id', uuid);
        keyImgDiv.html(idx + ' - ' + label);
        let _self = this;
        keyImgDiv.on('click', function(ev) {
            _self.selectAnnotationByUuid(ev.target.id, false);
        });
        container.append(keyImgDiv);
    }

    showAnnotationCount() {
        $('#lblAnnotationCount').html('(' + this._arrAnnotations.length + ')');
    }
 }