import GlobalVars from "../globalvars";
import Annotation from "./annotation";

export default class Polygon extends Annotation {
    constructor(type, parent, fabricCanvas, uuid, position) {
        super(type, parent, fabricCanvas, uuid, position);

        // this._pointArray = new Array();
        // this._lineArray = new Array();
        // this._activeLine = null;

        this._isFinished = false;
    }

    _init() {
        console.log('polygon init, type: ' + this._type);
        let origX = this._initPosition.x;
        let origY = this._initPosition.y;
        console.log('origX: ' + origX + ', origY: ' + origY);

        this._pointArray = new Array();
        this._lineArray = new Array();
        this._activeLine = null;

        this.addPoint(this._initPosition);
    }

    onCreating(pointer) {
        if(this._activeLine && this._activeLine.class == "line") {
            this._activeLine.set({ x2: pointer.x, y2: pointer.y });

            var points = this._fabricObj.get("points");
            points[this._pointArray.length] = {
                x:pointer.x,
                y:pointer.y
            }
            this._fabricObj.set({
                points: points
            });
        }
    }

    onCreatingClick(options) {
        if(options.target && options.target.id == this._pointArray[0].id) {
            this.generatePolygon(this._pointArray);
            var _self = this;
            setTimeout(function(ev) {
                _self.onFinish();
            }, 50);
            return true;
        }
        else {
            let pointer = this._fabricCanvas.getPointer(options.e);
            this.addPoint(pointer, true);
            return false;
        }
    }

    addPoint(pointer, isCreating = true) 
    {
        let curZoom = this._fabricCanvas.getZoom();
        let id = this._uuid + '_' + this._pointArray.length;
        var circle = new fabric.Circle({
            radius: GlobalVars.ANCHOR_RADIUS, // / curZoom,
            fill: (isCreating) ? this._strokeColorActive : this._strokeColor,
            stroke: (isCreating) ? '#808080' : this._strokeColor,
            strokeWidth: 0.5 / curZoom,
            left: pointer.x, // (options.e.layerX/canvas.getZoom()),
            top: pointer.y, // (options.e.layerY/canvas.getZoom()),
            selectable: true,
            hasBorders: false,
            hasControls: false,
            originX:'center',
            originY:'center',
            id: id,
            uuid: this._uuid,
            type: "anchor",
            objectCaching: false
        });
        if(this._pointArray.length == 0 && isCreating) {
            circle.set({
                fill:'red'
            })
        }
        
        if(this._fabricObj != null) {
            // var pos = canvas.getPointer(options.e);
            let points = this._fabricObj.get("points");
            points.push({
                x: pointer.x, //pos.x,
                y: pointer.y, //pos.y
            });
            let polygon = new fabric.Polygon(points,{
                stroke: this._strokeColorActive,
                strokeWidth: this._strokeWidth / curZoom,
                fill: '#cccccc',
                opacity: 0.3,
                selectable: false,
                hasBorders: true,
                hasControls: false,
                evented: false,
                objectCaching:false,
                uuid: 'tmp_' + this._uuid,
            });
            this._fabricCanvas.remove(this._fabricObj);
            this._fabricCanvas.add(polygon);
            this._fabricObj = polygon;
            this._fabricCanvas.renderAll();
        }
        else {
            // var polyPoint = [{x:(options.e.layerX/canvas.getZoom()),y:(options.e.layerY/canvas.getZoom())}];
            let polyPoint = [{x: pointer.x, y: pointer.y}];
            let polygon = new fabric.Polygon(polyPoint, {
                stroke: this._strokeColorActive,
                strokeWidth: this.strokeWidth / curZoom,
                fill: '#cccccc',
                opacity: 0.3,
                selectable: false,
                hasBorders: true,
                hasControls: false,
                evented: false,
                objectCaching:false,
                uuid: 'tmp_' + this._uuid,
            });
            this._fabricObj = polygon;
            this._fabricCanvas.add(polygon);
        }

        if(isCreating) {
            let points = [pointer.x, pointer.y, pointer.x, pointer.y];
            let line = new fabric.Line(points, {
                stroke: this._strokeColorActive,
                strokeWidth: this._strokeWidth / curZoom,
                fill: '#999999',
                stroke: '#999999',
                class:'line',
                originX:'center',
                originY:'center',
                selectable: false,
                hasBorders: true,
                hasControls: false,
                evented: false,
                objectCaching:false,
                uuid: 'line_' + this._uuid,
            });
            this._activeLine = line;
            this._lineArray.push(line);
            this._fabricCanvas.add(line);
        }

        this._pointArray.push(circle);
        this._fabricCanvas.add(circle);
        circle.moveTo(Annotation.ANNOTATION_Z_INDEX.ANCHOR);
        
        // this._fabricCanvas.selection = false;
    }

    generatePolygon(pointArray)
    {
        console.log('generatePolygon');
        var _self = this;
        var points = new Array();
        $.each(pointArray,function(index,point) {
            points.push({
                x:point.left,
                y:point.top
            });
            // _self._fabricCanvas.remove(point);
        });
        $.each(this._lineArray,function(index,line){
            _self._fabricCanvas.remove(line);
        });
        this._fabricCanvas.remove(this._fabricObj);
        if(this._activeLine != null)
            this._fabricCanvas.remove(this._activeLine);

        console.log('stroke width: ' + this._strokeWidth + ', zoom: ' + this._fabricCanvas.getZoom());
        var polygon = new fabric.Polygon(points, {
            stroke: (this._isActive) ? this._strokeColorActive : this._strokeColor, //'#333333',
            strokeWidth: (this._strokeWidth / this._fabricCanvas.getZoom()),
            fill: '#cccccc40',
            opacity: 1.0, // 0.2,
            type: 'polygon',
            uuid: this._uuid,
            hasBorders: false,
            hasControls: false,
            lockMovementX: true,
            lockMovementY: true,
            selectable: true,
            objectCaching:false,
        });
        this._fabricCanvas.add(polygon);
        polygon.sendToBack();

        // polygon.moveTo(Annotation.ANNOTATION_Z_INDEX.MAIN_OBJECT);

        this._fabricObj = polygon;
        this._isFinished = true;

        this._activeLine = null;
        // this._fabricCanvas.selection = true; //TODO: no-need ?
    }

    getAnnotationData4Server() { 
        let dataObj = super.getAnnotationData4Server();

        //set list points
        dataObj.points = [];
        let points = this._fabricObj.get("points");
        points.forEach((point) => {
            dataObj.points.push({
                x: point.x,
                y: point.y
            });
        });

        return dataObj;
    }

    static createFromDataObj(dataObj, parent, fabricCanvas) {
        if(dataObj.points == undefined || dataObj.points.length == 0) {
            console.log('Wrong polygon data: ', dataObj);
            return null;
        }
        let points = dataObj.points;

        let anno = new Polygon(parseInt(dataObj.type), parent, fabricCanvas, dataObj.uuid, {
            x: points[0].x,
            y: points[0].y
        });

        let curZoom = fabricCanvas.getZoom();

        //points
        let len = points.length;
        for(let i = 1; i < len; i++) {
            console.log('here, ' + i);
            let id = anno._uuid + '_' + i;
            var circle = new fabric.Circle({
                radius: GlobalVars.ANCHOR_RADIUS / curZoom,
                fill: this._strokeColor,
                stroke: '#808080',
                strokeWidth: 0.5 / curZoom,
                left: points[i].x, // (options.e.layerX/canvas.getZoom()),
                top: points[i].y, // (options.e.layerY/canvas.getZoom()),
                selectable: true,
                hasBorders: false,
                hasControls: false,
                originX:'center',
                originY:'center',
                id: id,
                uuid: anno._uuid,
                type: "anchor",
                objectCaching: false
            });
            anno._pointArray.push(circle);
            anno._fabricCanvas.add(circle);

            circle.moveTo(Annotation.ANNOTATION_Z_INDEX.ANCHOR);
        }
        anno.generatePolygon(anno._pointArray);

        //label
        anno.setLabel(dataObj.label);

        //change color of point arrays
        anno.setActive(false);

        console.log('Polygon createFromDataObj end');

        return anno;
    }

    onMove(fabricObj) {
        //check if is main polygon or anchor points
        if(fabricObj.type != undefined && fabricObj.type === 'anchor') {
            console.log('polygon anchor moved');
            this.generatePolygon(this._pointArray);
        }
        else if(fabricObj.type == 'text') {
            let eps = 1.0 / this._fabricCanvas.getZoom();
            this._fabricTextShadow.set('left', fabricObj.left + eps);
            this._fabricTextShadow.set('top', fabricObj.top + eps);
            this._fabricTextShadow.setCoords();
        }
        else {
            console.log('polygon moved');
        }
    }

    setActive(isActive) {
        this._isActive = isActive;

        if(!this._isFinished)
            return;

        console.log('polygon setActive: ' + isActive);

        let curZoom = this._fabricCanvas.getZoom();

        //TODO: set color
        if(isActive) {
            if(this._fabricObj != null) {
                // this._fabricObj.set({
                //     stroke: this._strokeColorActive,
                //     // strokeWidth: this._strokeWidth / curZoom
                // });
                this._fabricObj.set("stroke", this._strokeColorActive);
            }
            if(this._fabricText != null)
                this._fabricText.set("stroke", this._textColorActive);

            this._pointArray.forEach((anchor) => {
                anchor.set('fill', this._strokeColorActive);
            });
        }
        else {
            if(this._fabricObj != null) {
                // this._fabricObj.set({
                //     stroke: this._strokeColor,
                //     // strokeWidth: this._strokeWidth / curZoom
                // });
                this._fabricObj.set("stroke", this._strokeColor);
            }
            if(this._fabricText != null)
                this._fabricText.set("stroke", this._textColor);

            this._pointArray.forEach((anchor) => {
                anchor.set('fill', this._strokeColor);
            });
        }
    }

    show() {
        super.show();
        this._pointArray.forEach((anchor) => {
            anchor.visible = true;
        });
    }

    hide() {
        super.hide();
        this._pointArray.forEach((anchor) => {
            anchor.visible = false;
        });
    }

    dispose() {
        this._pointArray.forEach((anchor) => {
            this._fabricCanvas.remove(anchor);
        });
        this._pointArray = [];
        super.dispose();
    }

    updateSizeByZoom(curZoom) {
        super.updateSizeByZoom(curZoom);
        let ratio = (curZoom > 2) ? (1.0 / curZoom) : 0.5;
        console.log('updateSizeByZoom, radius: ' + (GlobalVars.ANCHOR_RADIUS * ratio));
        this._pointArray.forEach((anchor) => {
            anchor.set({
                strokeWidth: 0.5 * ratio,
                radius: GlobalVars.ANCHOR_RADIUS * ratio
            });
            // anchor.set('strokeWidth', 0.5 * ratio);
            // anchor.set('radius', GlobalVars.ANCHOR_RADIUS * ratio);
        });
        if(this._fabricObj != null) {
            // this._fabricObj.set({
            //     strokeWidth: this._strokeWidth / curZoom
            // });
            this._fabricObj.set('strokeWidth', this._strokeWidth * ratio);
        }  
    }
}